import moment from 'moment';

export function getTimeAgoString(dateOfCreationPost) {
    let postDate = Date.parse(dateOfCreationPost);
    let dateNow = Date.now();
    let duration = moment.duration(dateNow - postDate);

    let resultString = "";

    let [years, months, days, hours, minutes, seconds] =
        [duration.years(), duration.months(), duration.days(), duration.hours(), duration.minutes(), duration.seconds()];

    if (years > 0)
        resultString = `${years} .`;
    else if (months > 0)
        resultString = `${months} .`;
    else if (days > 0)
        resultString = `${days} .`;
    else if (hours > 0)
        resultString = `${hours} .`;
    else if (minutes > 0)
        resultString = `${minutes} .`;
    else if (seconds > 0)
        resultString = `${seconds} .`;
    else
        return " ";

    return resultString + " ";
}

export function getReleaseYear(date) {
    return date !== null ? (new Date(Date.parse(date))).getFullYear() : null;
}

export function getHumanRuntime(minutes) {
    if (minutes === null)
        return null;

    let resultString = "";

    if (minutes > 60)
        resultString += `${Math.floor(minutes / 60)}  `;

    let onlyMinutes = minutes % 60;

    if (onlyMinutes > 0)
        resultString += `${onlyMinutes} `;

    return resultString;
}
